

#main {
  background: #dfdfdf;
  color: black;
  border: 1px solid #000;
  border-radius: 20px;
  -webkit-box-shadow: 3px 3px 10px black;
          box-shadow: 3px 3px 10px black;
  width: 30em;
  height: 40em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

#screen{
  border-style: solid;
  border-color: #4caf50;
  margin-bottom: 20px;
  padding-left: 20px;
  padding-right: 30px;
  background-color: #212121;
  height: 80px;
  width: 90%;
  text-align: right;
}

#screen div {
  padding-top: 10px;
  font-size: 30px;
  font-weight: 900;
  color: white;
  line-height: 25px;
}

#screen div.text-small {
  position: relative;
  padding-top: 0;
  font-size: 15px;
}

#screen div#calculations,
#screen div#stored-value{
  font-size: 15px;
}

#screen div.text-small #stored-value {
  position: absolute;
  top: 0;
  text-align: left;
}

button {
  font-size: 20px;
  display: inline-block;
  width: 70px;
  height: 50px;
  margin: 5px;
  background-color: #43a047;
  height: 45px;
  color: #f3e5f5;
  border-color: #f3e5f5;
  
}


#keypad-container {
  -ms-flex-pack: center;
      justify-content: center;
  padding: 10px;
  margin-top: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
  height: 45%;
}

#keypad-container button {
  -ms-flex-preferred-size: 30%;
      flex-basis: 30%;
  -ms-flex-item-align: center;
      align-self: center;
}




